import React from "react";
import { connect } from "react-redux";
import { endSetup } from "../actions/index";

const mapStateToProps = state => {
    return { screen: state.screen };
};

const mapDispatchToProps = dispatch => {
    return {
        endSetup: () => dispatch(endSetup()),
    };
};



const ButtonDiv = ({ screen, endSetup }) => {
    const handleSubmit = (event) => {
        event.preventDefault();
        console.log("switch screen");
        endSetup();
    };

    const label = (screen === "SETUP") ? "Start Game" : "Return to Setup";

    return(
    <div>
        <form onSubmit={handleSubmit}>
            <button type="submit">{label}</button>
        </form>
    </div>
    )
};

const AppButtons = connect(mapStateToProps, mapDispatchToProps)(ButtonDiv);

export default AppButtons;