import React from "react";
import { connect } from "react-redux";

import { setStartingHP } from "../actions/index";
import HealthTracker from "./HealthTracker";

const mapStateToProps = state => {
    return { 
        startHP1: state.startHP.gravehold,
        startHP2: state.startHP.nemesis,  
    };
};

const mapDispatchToProps = dispatch => {
    return {
        setStartingHP: (data) => dispatch(setStartingHP(data)),
    };
};

const UI = ({ startHP1, startHP2, setStartingHP }) => {
    const onChange1 = (hp) => {
        setStartingHP({
            type: "gravehold",
            value: hp
        });
    }

    const onChange2 = (hp) => {
        setStartingHP({
            type: "nemesis",
            value: hp
        });
    }

    return (
    <div className="healthArea">
        <HealthTracker name="gravehold" startHP={startHP1} hp={startHP1} setHP={onChange1} />
        <HealthTracker name="nemesis" startHP={startHP2} hp={startHP2} setHP={onChange2} />
    </div>
    )
}

const Deck = connect(mapStateToProps, mapDispatchToProps)(UI);
export default Deck;