import React, { Component } from "react";
import { connect } from "react-redux";

import { returnCard } from "../actions/index";
import CardFront from "./CardFront";

const mapStateToProps = state => {
    return { cards: state.inDiscard };
};

const mapDispatchToProps = dispatch => {
    return {
        returnCard: () => dispatch(returnCard()),
    };
};

const UI = ({ cards, returnCard }) => {
    const onClick = () => {
        returnCard();
    }

    function onHover (event) {
        event.target.classList.add("hovering");
    }

    function onLeave (event) {
        event.target.classList.remove("hovering");
    }

    function getCard (card, index) {
        if (index === 0) {
            return (
                <CardFront key={card.id} card={card} 
                    onClick={onClick} onMouseEnter={onHover} onMouseLeave={onLeave}
                />
            );
        }
        return <CardFront key={card.id} card={card}/>;
    }

    return (
    <div className="discardHolder">
        {cards.map(getCard)}
    </div>
    )
}


const DiscardPile = connect(mapStateToProps, mapDispatchToProps)(UI);
export default DiscardPile;