import React, { Component } from "react";
import { connect } from "react-redux";

import { shuffleCards } from "../actions/index";

const mapStateToProps = state => {
    return { cards: state.inDiscard };
};

const mapDispatchToProps = dispatch => {
    return {
        shuffleCards: () => dispatch(shuffleCards()),
    };
};

const UI = ({ cards, shuffleCards }) => {
    const onClick = () => {
        shuffleCards();
    }

    return (
    <button onClick={onClick}>Shuffle All</button>
    )
}


const PlayActions = connect(mapStateToProps, mapDispatchToProps)(UI);
export default PlayActions;