import React from "react";
import { connect } from "react-redux";

import { setCurrentHP } from "../actions/index";
import HealthTracker from "./HealthTracker";

const mapStateToProps = state => {
    return { 
        currentHP1: state.currentHP.gravehold,
        currentHP2: state.currentHP.nemesis,
        startHP1: state.startHP.gravehold,
        startHP2: state.startHP.nemesis,  
    };
};

const mapDispatchToProps = dispatch => {
    return {
        setCurrentHP: (data) => dispatch(setCurrentHP(data)),
    };
};

const UI = ({ currentHP1, currentHP2, startHP1, startHP2, setCurrentHP }) => {
    const setHP1 = (hp) => {
        setCurrentHP({
            type: "gravehold",
            value: hp
        });
    }

    const setHP2 = (hp) => {
        setCurrentHP({
            type: "nemesis",
            value: hp
        });
    }

    return (
    <div className="healthArea">
        <HealthTracker name="gravehold" startHP={startHP1} hp={currentHP1} setHP={setHP1} />
        <HealthTracker name="nemesis" startHP={startHP2} hp={currentHP2} setHP={setHP2} />
    </div>
    )
}


const CurrentHealth = connect(mapStateToProps, mapDispatchToProps)(UI);
export default CurrentHealth;