import { 
    ADD_CARD, REMOVE_CARD, SHUFFLE_CARDS, END_SETUP, REVEAL_CARD, RETURN_CARD, SET_STARTINGHP, SET_CURRENTHP
} from "../constants/action-types";

const initialState = {
    articles: [],
    cards: [
        {name: "Nemesis", id: "default1"},
        {name: "Nemesis", id: "default2"},
    ],
    screen: "SETUP",
    inDiscard: [],
    inDeck: [],
    startHP: {gravehold: 30, nemesis: 70},
    currentHP: {gravehold: 30, nemesis: 70},
};

function shuffleArray(array) {
    for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
    }
}

const rootReducer = (state = initialState, action) => {
    let cards, hp;

    switch (action.type) {
        case END_SETUP:
            if (state.screen === "SETUP") {
                const cards = state.cards.slice();
                shuffleArray(cards);
                return {
                    ...state, 
                    screen: "PLAY", 
                    inDeck: cards, 
                    inDiscard: [],
                    currentHP: Object.assign({}, state.startHP),
                };
            }
            return {...state, screen: "SETUP"};
        case ADD_CARD:
            return {...state, cards: [...state.cards, action.payload] };
        case REMOVE_CARD:
            const idToRemove = action.payload.id;
            cards = state.cards.filter(card => {
                return card.id !== idToRemove;
            })
            return {...state, cards: cards};
        case SHUFFLE_CARDS:
            cards = state.cards.slice();
            shuffleArray(cards);
            return { ...state, inDeck: cards, inDiscard: [] };
        case REVEAL_CARD:
            return {
                ...state,
                inDeck: state.inDeck.slice(1),
                inDiscard: [state.inDeck[0], ...state.inDiscard]
            }
        case RETURN_CARD:
            return {
                ...state,
                inDiscard: state.inDiscard.slice(1),
                inDeck: [state.inDiscard[0], ...state.inDeck]
            }
        case SET_STARTINGHP:
            hp = Object.assign(state.startHP);
            hp[action.payload.type] = action.payload.value;
            return {...state, startHP: hp};
        case SET_CURRENTHP:
            hp = Object.assign(state.currentHP);
            hp[action.payload.type] = action.payload.value;
            return {...state, currentHP: hp};
        default:
            return state;
    }
};

export default rootReducer;