import React, { Component } from "react";
import { connect } from "react-redux";

import { endSetup } from "../actions/index";

const mapStateToProps = state => {
    return { screen: state.screen };
};

const mapDispatchToProps = dispatch => {
    return {
        switchTab: () => dispatch(endSetup()),
    };
};

const UI = ({ screen, switchTab }) => {
    const onClick = (event) => {
        if (event.target.id !== screen) {
            switchTab();
        }
    }

    function getClass (name) {
        if (name === screen) {
            return "activeTab";
        }
        return "inactiveTab";
    }

    return (
    <div className="pageTabsArea">
        <div id="SETUP" className={getClass("SETUP")} onClick={onClick}>SETUP</div>
        <div id="PLAY" className={getClass("PLAY")}  onClick={onClick}>PLAY GAME</div>
    </div>
    )
}


const PageTabs = connect(mapStateToProps, mapDispatchToProps)(UI);
export default PageTabs;