import React from "react";
import { connect } from "react-redux";

import CardFront from "./CardFront";
import { removeCard } from "../actions/index";

const mapStateToProps = state => {
    return { cards: state.cards };
};

const mapDispatchToProps = dispatch => {
    return {
        removeCard: (payload) => dispatch(removeCard(payload)),
    };
};

const UI = ({ cards, removeCard }) => {
    const onClick = (event) => {
        removeCard({id: event.target.id});
    }

    return (
    <div className="discardHolder">
        {cards.map((card) => (
            <CardFront key={card.id} card={card} onClick={onClick} />
        ))}
    </div>
    )
}

const List = connect(mapStateToProps, mapDispatchToProps)(UI);

export default List;