import React, { Component } from "react";
import { connect } from "react-redux";
import uuidv1 from "uuid";
import { addCard } from "../actions/index";
import CardFront from "./CardFront";

const mapDispatchToProps = dispatch => {
    return {
        addCard: article => dispatch(addCard(article))
    };
};

class ConnectedForm extends Component {
    constructor() {
        super();

        this.state = {
            title: "",
            selectedOption: "",
        };

        this.onClick = this.onClick.bind(this);

        this.cards = [
            {id: "nemesis", class: "nemesis"},
            {id: "one", class: "one"},
            {id: "two", class: "two"},
            {id: "three", class: "three"},
            {id: "four", class: "four"},
            {id: "onetwo", class: "onetwo"},
            {id: "threefour", class: "threefour"},
            {id: "wild", class: "wild"}
        ];
    }

    handleOptionChange(event) {
        this.setState({ selectedOption: event.target.value });
    }

    onClick (event) {
        const cardId = event.target.id;
        const index = this.cards.findIndex(card => {
            return (card.id === cardId);
        });
        if (index >= 0) {
            const card = this.cards[index];
            const id = uuidv1();
            this.props.addCard({ name: card.name, id, class: card.class });
        }
    }

    render() {
        //const that=this;
        const GetButtons = (props) => {
            const name = props.card;
            return(
                <div className="radio">
                    <label>
                        <input
                            type="radio"
                            value={name}
                            checked={this.state.selectedOption === name}
                            onChange={this.handleOptionChange}
                        />
                        {name}
                    </label>
                </div>
            )
        };

        return(
            <div className="discardHolder">
                {this.cards.map((card) => (
                    <CardFront key={card.id} card={card} onClick={this.onClick} />
                ))}
            </div>
        );
    }
}

const CardList = connect(null, mapDispatchToProps)(ConnectedForm);

export default CardList;