import React from "react";

const getHealthClass = (startHP, hp) => {
    if (hp >= startHP) {
        return "healthNumber goodHealth";
    }
    if (hp < startHP/2) {
        return "healthNumber badHealth";
    }
    return "healthNumber okHealth";
}

const HealthTracker = ({ name, startHP, hp, setHP }) => {
    function getClassName () {
        return "healthTracker " + name;
    }

    function onChangeHP (event) {
        setHP(event.target.value);
    }

    function increaseHP () {
        setHP(hp + 1);
    }

    function decreaseHP () {
        setHP(hp - 1);
    }

    return (
    <div className={getClassName()}>
        <div className="healthButton" onClick={decreaseHP}>-</div>
        <input className={getHealthClass(startHP, hp)} type="text" value={hp} onChange={onChangeHP}/>
        <div className="healthButton" onClick={increaseHP}>+</div>
    </div>
    )
}

export default HealthTracker;