import { 
    ADD_CARD, REMOVE_CARD, SHUFFLE_CARDS, END_SETUP, REVEAL_CARD, RETURN_CARD, SET_STARTINGHP, SET_CURRENTHP
} from "../constants/action-types";

export const setStartingHP = hp => ({
    type: SET_STARTINGHP,
    payload: {
        type: hp.type,
        value: hp.value
    }
});

export const setCurrentHP = hp => ({
    type: SET_CURRENTHP,
    payload: {
        type: hp.type,
        value: hp.value
    }
});

export const addCard = card => ({ 
    type: ADD_CARD, payload: {
        id: card.id,
        name: card.name,
        class: card.class
    }
});

export const removeCard = card => ({ 
    type: REMOVE_CARD, payload: {
        id: card.id
    }
});

export const endSetup = () => ({
    type: END_SETUP, payload: {}
});

export const shuffleCards = () => ({
    type: SHUFFLE_CARDS, payload: {}
});

export const revealCard = () => ({
    type: REVEAL_CARD, payload: {}
});

export const returnCard = () => ({
    type: RETURN_CARD, payload: {}
});
