import React from "react";
import { connect } from "react-redux";

import { revealCard, shuffleCards } from "../actions/index";

const mapStateToProps = state => {
    return { cards: state.inDeck };
};

const mapDispatchToProps = dispatch => {
    return {
        revealCard: () => dispatch(revealCard()),
        shuffleCards: () => dispatch(shuffleCards()),
    };
};

const UI = ({ cards, revealCard, shuffleCards }) => {
    const getCard = (card, index) => {
        const onClick = () => {
            revealCard();
        }
    
        const divStyle = {
            left: index*5,
            zIndex: index
        };
        return (
            <div className="cardBack" onClick={onClick} style={divStyle}/>
        );
    }

    function doShuffle () {
        shuffleCards();
    }

    function getWidth () {
        const width = (110 + (cards.length * 8));
        return { width: width + 'px' };
    }

    const width = (110 + (cards.length * 5));

    // setTimeout(() => {
    //     const cardBacks = document.getElementsByClassName("cardBack");
    //     Array.from(cardBacks).forEach((cardBack, index) => {
    //         cardBack.style.left = (index*5) + "px";
    //     });
    // }, 500);

    if (cards.length === 0) {
        return (
            <div className="deckHolder" style={{minWidth: width}}>
                <div className="shuffleHolder" onClick={doShuffle}>Shuffle</div>
            </div>
        )    
    }

    return (
    <div className="deckHolder" style={{minWidth: width}}>
        {cards.map(getCard)}
    </div>
    )
}


const Deck = connect(mapStateToProps, mapDispatchToProps)(UI);
export default Deck;